import React, { Component } from 'react';
import FiveElements from './FiveElements';
import ShadowLayer from './ShadowLayer';
let image_counter = 0;
class Title extends Component {

    changeImage = () => {
        let link = this.props.newData.imgurl[0].link;

        return link;
    }
    loopImage = () => {
        setInterval(this.changeImages, 2000);
    }
    changeImages = () => {
        let link = this.props.newData.imgurl[image_counter % 10].link;
        document.getElementById("img-holder").src = link;
        image_counter++;
    }

    render() {
        return (
            <React.Fragment>
                <div className="row box1" id="box1id">
                    <div className="col-12 col-md-12 col-xl-7 box3">
                        <h1>Local Weather Six Hour Forecast</h1>
                        <h3>Northern California with dropler radar</h3>
                        <p></p>
                        <form onSubmit={this.props.handleGetWeather}>
                            <span className="forcast">Forcast for</span> <input className="searchinput" type="text" placeholder="ex: San Francisco" name="city"></input>
                            <button onClick={this.props.handleOnclick} className="btn m-3 submit">submit</button>
                            <br />
                        </form>
                        <div className="box4">
                            <span className="currenttime ">{this.props.newData.current} </span>  <span className="currenttime m-3">{this.props.newData.weathervalue[0].time}</span> <img src={this.props.newData.weathervalue[0].icon} /> <span className="degree m-3">  {this.props.newData.weathervalue[0].degree}</span>
                        </div>
                        <div className="row">
                            <div className="col-12 col-md-6 box6">
                                <div className="icons"><img src={this.props.newData.weathervalue[0].icon} /></div>
                                <div className="row text-center leftbox">
                                    <div style={{ display: this.props.newData.display }} className="col-6 col-md-6 box7">
                                        <span className="currenttime ">{this.props.newData.current} </span><br /> <span className="currenttime m-3">{this.props.newData.weathervalue[0].time}</span>
                                    </div>
                                    <div className="col-6 col-md-6 box8">
                                        <span className="degree m-3">  {this.props.newData.weathervalue[0].degree}</span>
                                    </div>
                                </div>
                                <div className="icons1">  <img className="arrow" onClick={this.props.arrowOnclick} src={this.props.newData.arrowup} /></div>
                            </div>
                            <div className="col-md-6 box9">
                                <img className="threpics threone" src={this.changeImage()} onLoad={this.loopImage} ></img>
                                <img className="threpics" src={this.props.newData.droplet[0].link} ></img>
                                <img className="threpics" src={this.props.newData.droplet[1].link} ></img>
                            </div>
                        </div>
                    </div>

                    <div className="col-lg-12  col-xl-5 box2">
                        <form onSubmit={this.props.handleGetWeather}>
                            <img className="threpics threone" src={this.changeImage()} onLoad={this.loopImage} id="img-holder"></img>
                            <img className="threpics" src={this.props.newData.droplet[0].link} id="img-holder"></img>
                            <img className="threpics" src={this.props.newData.droplet[1].link} id="img-holder"></img>
                        </form>
                    </div>
                </div>
                <ShadowLayer newData={this.props.newData} />
                <FiveElements newData={this.props.newData} handleOnclick={this.props.handleOnclick} arrowOnclickUp={this.props.arrowOnclickUp} changeClassname={this.props.changeClassname} />
            </React.Fragment>

        );
    }
}

export default Title;