import React, { Component } from 'react';
class ShadowLayer extends Component {
    render() {
        return (
            <div id="liboxx" className="row text-center justify-content-center libox">

                <div className="col-md col-sm-12 text-center libox1">
                    <span className="clock">{this.props.newData.weathervalue[1].time}</span>
                </div>
                <div className="col-md col-sm-12 text-center libox1">
                    <span className="clock">{this.props.newData.weathervalue[2].time}</span>
                </div>
                <div className="col-md col-sm-12 text-center libox1">
                    <span className="clock">{this.props.newData.weathervalue[3].time}</span>
                </div>
                <div className="col-md col-sm-12 text-center libox1">
                    <span className="clock">{this.props.newData.weathervalue[4].time}</span>
                </div>
                <div className="col-md col-sm-12 text-center libox1">
                    <span className="clock">{this.props.newData.weathervalue[5].time}</span>
                </div>
            </div >
        );
    }
}

export default ShadowLayer;