import React, { Component } from 'react';
import './App.css';
import Title from './title';
const API_KEY = '64fcc070bac9965e73d62f77086c9a04';
let count1 = 0;
let count2 = 0;

let imageArray = [];

class App extends Component {

  state = {
    toggle: true,
    arrowup: undefined,
    arrow: undefined,
    display: 'none',
    droplet: [
      { link: undefined },
      { link: undefined },
    ],
    imgurl: [
      { link: undefined },
      { link: undefined },
      { link: undefined },
      { link: undefined },
      { link: undefined },
      { link: undefined },
      { link: undefined },
      { link: undefined },
      { link: undefined },
      { link: undefined }
    ],
    current: undefined,
    weathervalue: [
      { id: 1, time: undefined, icon: undefined, degree: undefined },
      { id: 2, time: undefined, icon: undefined, degree: undefined },
      { id: 3, time: undefined, icon: undefined, degree: undefined },
      { id: 4, time: undefined, icon: undefined, degree: undefined },
      { id: 5, time: undefined, icon: undefined, degree: undefined },
      { id: 6, time: undefined, icon: undefined, degree: undefined },
    ]
  };
  arrowOnclick = () => {
    document.getElementById("fiveid").style.zIndex = '1';
    document.getElementById("fiveid").classList.remove('fiveout');
    document.getElementById("fiveid").classList.add('fivebox');
    document.getElementById("liboxx").style.visibility = "hidden";
    // setTimeout(() => {
    //   document.getElementById("fiveid").classList.remove('fivebox');
    //  }, 11000);
    //document.getElementById("fiveid").style.display = "flex";
    //document.getElementById("box1id").style.display = "none";
    this.setState({ toggle: false });
  }
  arrowOnclickUp = () => {

    document.getElementById("fiveid").classList.remove('fivebox');
    document.getElementById("fiveid").classList.add("fiveout");
    document.getElementById("liboxx").style.visibility = "visible";
    setTimeout(() => {
      document.getElementById("fiveid").style.zIndex = '-1';
    }, 1200);

    //setTimeout(() => {
    // document.getElementById("fiveid").classList.remove('fiveout');
    // document.getElementById("fiveid").style.display = "none";
    // document.getElementById("box1id").style.display = "flex";
    //}, 11000);

  }
  changeClassname = () => {
    let classes = 'row text-center justify-content-center';
    if (this.state.toggle == true) {
      classes = classes;
    } else {
      classes = classes;
    }
    return classes;
  }
  handleOnclick = () => {
    this.setState({ display: 'block' });
  }
  addToArray = (newImage) => {

    if (count1 < 10) {
      imageArray.push(newImage);
      count1 = count1 + 1;
      if (count1 >= 10) {
        console.log("Got 10 doppler images");
      }
    }
  }
  // this will be executed first inorder to get 10 images into the
  // global imagesArray
  UNSAFE_componentWillMount = () => {
    let dateObj = new Date();  // defaults to current date and time
    // if we try 150 images, and get one out of every 10, we should get enough
    for (let i = 0; i < 150; i++) {
      let newImage = this.tryToGetImage(dateObj);
      dateObj.setMinutes(dateObj.getMinutes() - 1); // back in time one minute
    }
  }
  //Trying to get  the droppler
  tryToGetImage = (dateObj) => {
    let dateStr = dateObj.getUTCFullYear();
    dateStr += String(dateObj.getUTCMonth() + 1).padStart(2, '0'); //January is 0!
    dateStr += String(dateObj.getUTCDate()).padStart(2, '0');

    let timeStr = String(dateObj.getUTCHours()).padStart(2, '0')
    timeStr += String(dateObj.getUTCMinutes()).padStart(2, '0');

    let filename = "DAX_" + dateStr + "_" + timeStr + "_N0R.gif";
    let newImage = new Image();
    let url = "http://radar.weather.gov/ridge/RadarImg/N0R/DAX/" + filename;
    newImage.src = "http://radar.weather.gov/ridge/RadarImg/N0R/DAX/" + filename;
    newImage.onload = () => {
      //console.log("got image " + filename);
      this.addToArray(url);
    }
    newImage.onerror = () => {
      // console.log("failed to load "+filename);
    }


  }

  // this function will return the 
  // current date in pacific time zone
  getTime = () => {
    let date = new Date();
    return date.getHours();
  }
  // this function receive and handle
  // the api request from the client
  getWeather = async (e) => {

    //console.log(imageArray);
    e.preventDefault();

    var city = e.target.elements.city.value;
    let api_call = await fetch(`https://api.openweathermap.org/data/2.5/forecast?q=${city},us&units=metric&APPID=${API_KEY}`);
    const data = await api_call.json();

    const { weathervalue } = this.state;
    let count = 0;

    // getting pacific time zone
    let currentTime = this.getTime();
    // this will decide if the time is at day 
    // or at night
    let postFix = ['AM', 'PM'];


    weathervalue.map(x => {
      // getting 6 hours of temperatures
      x.degree = Math.round(data.list[count].main.temp) + "Â°";


      // handle time between AM and PM and getting icons coresponding to the description
      if (currentTime > 12) {
        //handle night icon
        if (data.list[count].weather[0].description.replace(/\s/g, '') === 'fewclouds' || data.list[count].weather[0].description.replace(/\s/g, '') === 'rain' || data.list[count].weather[0].description.replace(/\s/g, '') === 'clearsky') {
          x.icon = './assets/' + data.list[count].weather[0].description.replace(/\s/g, '') + '-night.svg';
        } else {
          x.icon = './assets/' + data.list[count].weather[0].description.replace(/\s/g, '') + '.svg';
        }
        //handle time
        currentTime === 24 ? x.time = (currentTime - 12) + ':00' + postFix[0] : x.time = (currentTime - 12) + ':00' + postFix[1];
        currentTime === 24 ? currentTime = 0 : currentTime = currentTime;
        currentTime++;
      } else {
        //handle day and night icon
        if (currentTime < 5) {
          if (data.list[count].weather[0].description.replace(/\s/g, '') === 'fewclouds' || data.list[count].weather[0].description.replace(/\s/g, '') === 'rain' || data.list[count].weather[0].description.replace(/\s/g, '') === 'clearsky') {
            x.icon = './assets/' + data.list[count].weather[0].description.replace(/\s/g, '') + '-night.svg';
          } else {
            x.icon = './assets/' + data.list[count].weather[0].description.replace(/\s/g, '') + '.svg';
          }
        } else if (data.list[count].weather[0].description.replace(/\s/g, '') === 'fewclouds' || data.list[count].weather[0].description.replace(/\s/g, '') === 'rain') {
          x.icon = './assets/' + data.list[count].weather[0].description.replace(/\s/g, '') + '-day.svg';
        } else {
          x.icon = './assets/' + data.list[count].weather[0].description.replace(/\s/g, '') + '.svg';
        }

        //handle time
        currentTime === 12 ? x.time = currentTime + ':00' + postFix[1] : x.time = currentTime + ':00' + postFix[0];
        currentTime++;
      }
      count++;
    });
    count = 0;
    const { imgurl } = this.state;
    imgurl.map(x => {
      x.link = imageArray[count2];
      count2++;
    });

    count2 = 0;

    const { droplet } = this.state;
    droplet[0].link = 'http://radar.weather.gov/ridge/Overlays/Topo/Short/DAX_Topo_Short.jpg';
    droplet[1].link = 'http://radar.weather.gov/ridge/Overlays/Cities/Short/DAX_City_Short.gif';

    this.setState({ weathervalue: weathervalue, imgurl: imgurl, current: 'Current', droplet: droplet, arrow: './assets/downArrow.svg', arrowup: './assets/upArrow.svg' });


  };
  render() {
    return (
      <React.Fragment>
        <Title handleGetWeather={this.getWeather} newData={this.state} handleOnload={this.handleOnload} handleOnclick={this.handleOnclick} changeClassname={this.changeClassname} arrowOnclick={this.arrowOnclick} arrowOnclickUp={this.arrowOnclickUp} />
      </React.Fragment>

    );
  }
}

export default App;

