import React, { Component } from 'react';
import './FiveElements.css';
class FiveElements extends Component {

    render() {
        return (
            <React.Fragment>
                <div className={this.props.changeClassname()} id="fiveid">
                    <div className="icons3">  <img className="arrow1" onClick={this.props.arrowOnclickUp} src={this.props.newData.arrow} /></div>
                    <div style={{ display: this.props.newData.display }} className="col-md col-sm-12 text-center colborder">
                        <span className=" clock1">{this.props.newData.weathervalue[1].time}</span>
                        <img src={this.props.newData.weathervalue[1].icon} alt={this.props.newData.weathervalue[1].icon} className="" />
                        <br className="break" />
                        <span className="fivedegrees">{this.props.newData.weathervalue[1].degree} </span>
                    </div>
                    <div style={{ display: this.props.newData.display }} className="col-md col-sm-12 text-center colborder">
                        <span className=" clock1">{this.props.newData.weathervalue[2].time}</span>
                        <img src={this.props.newData.weathervalue[2].icon} alt={this.props.newData.weathervalue[2].icon} className="" />
                        <br className="break" />
                        <span className="fivedegrees">{this.props.newData.weathervalue[2].degree} </span>
                    </div>
                    <div style={{ display: this.props.newData.display }} className="col-md col-sm-12 text-center colborder">
                        <span className=" clock1">{this.props.newData.weathervalue[3].time}</span>
                        <img src={this.props.newData.weathervalue[3].icon} alt={this.props.newData.weathervalue[3].icon} className="" />
                        <br className="break" />
                        <span className="fivedegrees">{this.props.newData.weathervalue[3].degree} </span>
                    </div>
                    <div style={{ display: this.props.newData.display }} className="col-md col-sm-12 text-center colborder">
                        <span className=" clock1">{this.props.newData.weathervalue[4].time}</span>
                        <img src={this.props.newData.weathervalue[4].icon} alt={this.props.newData.weathervalue[4].icon} className="" />
                        <br className="break" />
                        <span className="fivedegrees">{this.props.newData.weathervalue[4].degree} </span>
                    </div>
                    <div style={{ display: this.props.newData.display }} className="col-md col-sm-12 text-center colborder">
                        <span className=" clock1">{this.props.newData.weathervalue[5].time}</span>
                        <img src={this.props.newData.weathervalue[5].icon} alt={this.props.newData.weathervalue[5].icon} className="" />
                        <br className="break" />
                        <span className="fivedegrees">{this.props.newData.weathervalue[5].degree} </span>
                    </div>
                </div>
            </React.Fragment>
        );
    }
}

export default FiveElements;